import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import api from '@/common/vmeitime-http/'
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			cateMaskState: 0, //分类面板展开状态
			currentpage: 1,
			totalPages: 1,
			loadModal: true,
			searchtxt: '',
			// headerPosition:"fixed",
			// headerTop:"0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 0, 
			cateId: 0, //已选三级分类id
			priceOrder: 'asc', //1 价格从低到高 2价格从高到低
			cateList: [],
			goodsList: [],
			sort:0
		};
	},
	onLoad(options){
		this.cateId = options.tid;
		this.loadCateList(options.fid,options.sid);
		this.loadData();
	},
	//下拉刷新
	onPullDownRefresh(){
		this.currentpage = 1;
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom() {
		if(this.currentpage >= this.totalPages) return false
		this.currentpage++;
		this.loadData()
	},
	methods: {
		//加载分类
		async loadCateList(fid, sid){
			let list = await this.$api.json('cateList');
			let cateList = list.filter(item=>item.pid == fid);
			
			cateList.forEach(item=>{
				let tempList = list.filter(val=>val.pid == item.id);
				item.child = tempList;
			})
			this.cateList = cateList;
		},
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type='add') {
			uni.showLoading({title: '正在加载'})
			let categorylisturl = '/goods/list?categoryId='+this.cateId+'&page=' + this.currentpage + '&limit=10'
			api.httpget(categorylisturl).then((res) => {
				let respons = res.data;
				if(respons.errno == 0) {
					if(type === 'refresh'){
						this.goodsList = [];
					}
					this.goodsList = this.goodsList.concat(respons.data.list);
					this.totalPages = respons.data.pages
				}
				if(type === 'refresh'){
					uni.stopPullDownRefresh();
				}
				this.loadModal = false
				uni.hideLoading()
			}).catch((err) => {
				uni.hideLoading()
			})
			let listurl = '/goods/category?id=' + this.cateId
			api.httpget(listurl).then((res) => {
				let respons = res.data;
				if(respons.errno == 0) {
					uni.setNavigationBarTitle({
					    title: respons.data.currentCategory.name
					});
				}
				this.loadModal = false
				uni.hideLoading()
			}).catch((err) => {
				uni.hideLoading()
			})
		},
		searchinput() {
			uni.showLoading({title: '正在加载'})
			let url = '/goods/list?keyword='+this.searchtxt+'&page=1&limit=100&sort=name&order='+this.priceOrder+'&categoryId=0'
			api.httpget(url).then((res) => {
				let respons = res.data;
				if(respons.errno == 0) {
					this.goodsList = respons.data.list;
				}
				this.loadModal = false
				uni.hideLoading()
			}).catch((err) => {
				uni.hideLoading()
			})
		},
		//筛选点击
		tabClick(index){
			if(this.filterIndex === index && index !== 2){
				return;
			}
			this.filterIndex = index;
			this.sort = index
			if(index === 2){
				this.priceOrder = this.priceOrder === 'asc' ? 'desc': 'asc';
			}else{
				this.priceOrder = 'asc';
			}
			uni.pageScrollTo({
				duration: 300, scrollTop: 0
			})
			this.searchinput();
		},
		//显示分类面板
		toggleCateMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.cateMaskState = 2;
			setTimeout(()=>{
				this.cateMaskState = state;
			}, timer)
		},
		//分类点击
		changeCate(item){
			this.cateId = item.id;
			this.toggleCateMask();
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//详情
		navToDetailPage(item){
			let id = item.id;
			uni.navigateTo({
				url: `/pages/product/product?goodsId=${id}`
			})
		},
		stopPrevent(){}
	},
}
